import {
  initAbout,
  initAos,
  initAosScale,
  initButton,
  initFaqAccordion,
  initGradientHeadings,
  initLoad,
  initLoops,
  initNavigation,
  initSplitHead,
  initSplitLines,
  initStep,
  initSwiperAboutMobile,
  initSwiperTestiDesktop,
  initSwiperTestiMobile,
  // initThreeWithEXR,
} from './animations/clearedge';
import { initLenis } from './plugins/lenis';
import { backTop } from './utils/back-top';

CustomEase.create('primary-ease', '0.62, 0.05, 0.01, 0.99');
CustomEase.create('primary-ease-out', '0.17,0.84,0.44,1');
// ScrollTrigger.defaults({
//   invalidateOnRefresh: true,
// });

function initApp() {
  const mm = gsap.matchMedia();
  initLenis(); // inisialisasi dulu
  initAos();
  initAosScale();
  initStep();
  backTop();
  initAbout();
  initLoad();
  // initLoops();
  document.fonts.ready.then(() => {
    initSplitLines();
    initGradientHeadings();
  });
  initFaqAccordion();
  // initScrollTo();
  initButton();
  // initThreeWithEXR();

  mm.add('(min-width: 768px)', () => {
    initSwiperTestiDesktop(); // inisialisasi dulu
  });

  mm.add('(max-width: 767px)', () => {
    initSwiperTestiMobile(); // inisialisasi dulu
    initSwiperAboutMobile(); // inisialisasi dulu
    initNavigation();
  });
}
document.addEventListener('DOMContentLoaded', () => {
  initApp();
});
