export function initLoad() {
  const hero = document.querySelector('.hero.is-home');
  if (!hero) return;
  const heading = hero.querySelector('.heading-wrap');
  const paragraph = hero.querySelector('.text-16');
  const button = hero.querySelector('.btn');
  const light = hero.querySelector('.hero-light');
  const image = hero.querySelector('.hero-bg');
  const icon = hero.querySelector('[hero-icon]');

  const splitHeading = new SplitText(heading, {
    type: 'words, chars',
    wordsClass: 'words',
    charsClass: 'chars',
  });
  const splitParagraph = new SplitText(paragraph, {
    type: 'lines, words, chars',
    linesClass: 'lines',
  });

  gsap.set(splitHeading.chars, { yPercent: 101, autoAlpha: 0 });
  gsap.set(splitParagraph.words, { autoAlpha: 0, y: '101%' });
  gsap.set(button, { autoAlpha: 0, y: '2em' });
  gsap.set(light, { autoAlpha: 0, scale: 0.8, transformOrigin: 'left top' });
  // gsap.set(image, { y: '3em' });
  gsap.set(icon, { scale: 0 });

  const tl = gsap.timeline({ paused: true });

  tl.to(
    light,
    {
      autoAlpha: 1,
      scale: 1,
      duration: 1.2,
      ease: 'power2.out',
    },
    0
  )
    // .to(
    //   image,
    //   {
    //     y: '0%',
    //     duration: 1.2,
    //     ease: 'power2.out',
    //   },
    //   '<+0.4'
    // )
    .to(
      splitHeading.chars,
      {
        yPercent: 0,
        autoAlpha: 1,
        duration: 1.2,
        ease: 'power2.out',
        stagger: {
          amount: 0.8,
          from: 'start',
        },
      },
      '<+0.8'
    )
    .to(
      icon,
      {
        scale: 1,
        duration: 1.2,
        ease: 'power2.out',
      },
      '<+0.4'
    )
    .to(
      splitParagraph.words,
      {
        autoAlpha: 1,
        y: '0%',
        duration: 0.8,
        ease: 'power2.out',
        stagger: {
          amount: 0.6,
          from: 'start',
        },
      },
      '<+0.5'
    )
    .to(
      button,
      {
        autoAlpha: 1,
        y: '0em',
        duration: 1,
        ease: 'power2.out',
      },
      '<+0.6'
    );

  window.addEventListener('load', () => {
    // const lenis = getLenis();

    // requestAnimationFrame(() => {
    //   if (lenis) {
    //     // 1. reset dulu posisinya
    //     lenis.scrollTo(0, { immediate: true });

    //     // 2. langsung kunci scroll
    //     lenis.stop();
    //   } else {
    //     window.scrollTo(0, 0);
    //     document.body.style.overflow = 'hidden';
    //   }

    //   // animasi jalan setelah posisi fix
    //   tl.play();

    //   // 3. setelah anim selesai, baru kita unlock scroll
    //   tl.eventCallback('onComplete', () => {
    //     if (lenis) {
    //       lenis.start(); // unlock scroll
    //     } else {
    //       document.body.style.overflow = '';
    //     }
    //   });
    // });

    tl.play();
  });
}

