export function initAos() {
  const aos = document.querySelectorAll('[data-aos]');
  if (aos.length) {
    aos.forEach((el) => {
      const offset = parseFloat(el.getAttribute('data-aos-offset') || '3');
      gsap.from(el, {
        y: `${offset}em`,
        duration: 1.25,
        ease: 'power2.out',
        opacity: 0,
        scrollTrigger: {
          trigger: el,
          start: 'top 90%',
          markers: false,
        },
      });
    });
  }
}

export function initAosScale() {
  const aos = document.querySelectorAll('[data-aos-scale]');
  if (aos.length) {
    aos.forEach((el) => {
      gsap.from(el, {
        scale: 0,
        duration: 1.25,
        ease: 'power2.out',
        opacity: 0,
        scrollTrigger: {
          trigger: el,
          start: 'top 90%',
          markers: false,
        },
      });
    });
  }
}

export function initSplitLines() {
  const splitTargets = document.querySelectorAll('[data-split]');
  if (!splitTargets.length) return;

  splitTargets.forEach((el) => {
    if (!el || typeof el !== 'object' || !el.textContent.trim()) return;

    const split = new SplitText(el, {
      type: 'lines, words, chars',
      linesClass: 'lines',
      wordsClass: 'words',
      charsClass: 'chars',
    });

    gsap.set(split.words, { yPercent: 101, autoAlpha: 0 });

    ScrollTrigger.create({
      trigger: el,
      start: 'top 90%',
      markers: false,
      onEnter: () => {
        gsap.to(split.words, {
          yPercent: 0,
          autoAlpha: 1,
          duration: 1.25,
          ease: 'power2.out',
          stagger: {
            amount: 0.8,
            from: 'start',
          },
        });
      },
    });
  });
}

export function initGradientHeadings() {
  // Get all elements with [gradient-heading] attribute
  const gradientWrappers = document.querySelectorAll('[gradient-heading]');
  if (gradientWrappers.length === 0) return;

  gradientWrappers.forEach((wrapper) => {
    // Get gradient value from data attribute or use default
    const gradientValue =
      wrapper.getAttribute('data-gradient') ||
      'linear-gradient(177deg, rgba(255, 255, 255, 0.4) 0%,  rgba(255, 255, 255, 0.5) 12.35%, white 48.75%, rgba(255, 255, 255, 0.5) 100% )';

    // Get all text elements inside wrapper
    const textElements = wrapper.querySelectorAll('.h2');

    if (textElements.length === 0) return;

    const splits = [];
    const allWords = [];

    // Split each text element
    textElements.forEach((el) => {
      const split = new SplitText(el, {
        type: 'words',
        wordsClass: 'word',
      });
      splits.push(split);
      allWords.push(...split.words);
    });

    // Apply seamless gradient to each word
    function applySeamlessGradient() {
      const wrapperRect = wrapper.getBoundingClientRect();
      const wrapperWidth = wrapperRect.width;

      allWords.forEach((word) => {
        const wordRect = word.getBoundingClientRect();
        const offsetX = wordRect.left - wrapperRect.left;

        word.style.backgroundImage = gradientValue;
        word.style.backgroundSize = `${wrapperWidth}px 100%`;
        word.style.backgroundPosition = `-${offsetX}px 0`;
        word.style.webkitBackgroundClip = 'text';
        word.style.backgroundClip = 'text';
        word.style.webkitTextFillColor = 'transparent';
        word.style.color = 'transparent';
        word.style.display = 'inline-block';
      });
    }

    // Apply gradient
    applySeamlessGradient();

    // Set initial state
    gsap.set(allWords, {
      yPercent: 100,
      opacity: 0,
    });

    // Create ScrollTrigger animation
    ScrollTrigger.create({
      trigger: wrapper,
      start: 'center 90%',
      markers: true,
      onEnter: () => {
        gsap.to(allWords, {
          yPercent: 0,
          opacity: 1,
          duration: 0.8,
          stagger: 0.05,
          ease: 'power3.out',
          onComplete: applySeamlessGradient,
        });
      },
    });

    // Reapply gradient on resize
    let resizeTimeout;
    window.addEventListener('resize', () => {
      clearTimeout(resizeTimeout);
      resizeTimeout = setTimeout(applySeamlessGradient, 100);
    });
  });
}

export function initSplitHead() {
  const splitTargets = document.querySelectorAll('[data-heading]');
  if (!splitTargets.length) return;

  splitTargets.forEach((el) => {
    // if (!el || typeof el !== 'object' || !el.textContent.trim()) return;

    const split = new SplitText(el, {
      type: 'chars, words, lines',
      wordsClass: 'words',
      charsClass: 'chars',
      linesClass: 'lines',
    });

    gsap.set(split.chars, { yPercent: 101 });

    ScrollTrigger.create({
      trigger: el,
      start: 'top 90%',
      markers: false,
      onEnter: () => {
        gsap.to(split.chars, {
          yPercent: 0,
          duration: 1.25,
          ease: 'primary-ease-out',
          stagger: {
            amount: 0.2,
            from: 'start',
          },
        });
      },
    });
  });
}

export function initStep() {
  const content = document.querySelector('.step-content');
  if (!content) return;
  const items = content.querySelectorAll('.step-item');
  const progressWrap = content.querySelector('.step-progress-wrap');
  const progressLine = content.querySelector('.progress-line');

  if (!items.length || !progressLine) return;

  gsap.set(progressLine, { scaleY: 0 });

  gsap.to(progressLine, {
    scaleY: 1,
    ease: 'none',
    scrollTrigger: {
      trigger: progressWrap,
      start: 'top center',
      end: 'bottom center',
      scrub: true,
      markers: false,
    },
  });

  // Inisialisasi Rive
  const riveAnim = new rive.Rive({
    src: 'https://cdn.prod.website-files.com/68c1903510430b41f71cfa9a/68c85b3831a9fe503ade07bd_Section%202.riv',
    canvas: document.getElementById('riveCanvas'),
    autoplay: false,
    stateMachines: ['State Machine 1'], // ini biarkan, supaya state machine jalan
    onLoad: () => {
      console.log('Rive siap!');
      console.log('State Machines:', riveAnim.stateMachineNames);

      riveAnim.resizeDrawingSurfaceToCanvas();

      // ScrollTrigger sekali jalan
      ScrollTrigger.create({
        trigger: '#riveCanvas',
        start: 'top 90%',
        once: true,
        onEnter: () => {
          riveAnim.play(); // langsung play animasi state machine
          console.log('Scroll trigger aktif → play!');
        },
      });

      // Mouse hover trigger → langsung pakai play/pause
      const canvas = document.getElementById('riveCanvas');
      canvas.addEventListener('mouseenter', () => {
        riveAnim.play();
        console.log('Hover ON → play ulang');
      });

      canvas.addEventListener('mouseleave', () => {
        riveAnim.pause();
        console.log('Hover OFF → pause');
      });
    },
  });

  document.querySelectorAll('[data-lottie]').forEach((el, i) => {
    // ambil source JSON dari attribute
    const src = el.getAttribute('data-src');

    // load lottie
    const anim = lottie.loadAnimation({
      container: el,
      renderer: 'svg',
      loop: false,
      autoplay: false,
      path: src,
    });

    // ScrollTrigger untuk tiap animasi
    ScrollTrigger.create({
      trigger: el,
      start: 'top 90%',
      once: true,
      onEnter: () => {
        anim.play();
        console.log(`Lottie #${i + 1} play!`);
      },
    });
  });

  const video = document.querySelector('.step-video');

  gsap.from(video, {
    autoAlpha: 0,
    duration: 1.25,
    ease: 'primary-ease-out',
    scrollTrigger: {
      trigger: video,
      start: 'top 90%',
      markers: false,
    },
  });

  //   // ScrollTrigger kontrol
  //     ScrollTrigger.create({
  //       trigger: '#riveCanvas',
  //       start: 'top 90%', // mulai ketika bagian atas canvas 90% viewport
  //       once: true, // biar cuma jalan sekali
  //       onEnter: () => {
  //         riveAnim.play('OnLoad'); // ganti dengan nama animasi dari Rive Editor
  //       },
  //     });

  items.forEach((item, i) => {
    const number = item.querySelector('.step-number');
    if (!number) return;

    ScrollTrigger.create({
      trigger: number,
      start: 'center center',
      end: 'center center',
      onEnter: () => number.classList.add('is-active'),
      onEnterBack: () => number.classList.add('is-active'),
      onLeaveBack: () => number.classList.remove('is-active'),
    });
  });
}

export function initFaqAccordion(containerSelector = '.faq-content', allowMultipleOpen = false) {
  const container = document.querySelector(containerSelector);
  if (!container) return;

  const items = container.querySelectorAll('.faq-item');

  items.forEach((item, index) => {
    const text = item.querySelector('.faq-item-content');
    const icon = item.querySelector('.faq-icon.is-plus');
    if (!text || !icon) return;

    // Set ikon awal (kalau pakai rotate)
    gsap.set(icon, { rotate: 0 });
    gsap.set(text, { height: 0, autoAlpha: 0 });

    const tl = gsap.timeline({ paused: true, reversed: true });

    tl.to(text, {
      height: 'auto',
      duration: 0.4,
      ease: 'none',
    })
      .to(
        text,
        {
          autoAlpha: 1,
          duration: 0.3,
          ease: 'none',
        },
        0
      )
      .to(
        icon,
        {
          rotate: 90,
          duration: 0.3,
          ease: 'none',
        },
        0
      ); // ikon animasi dimulai bersamaan

    item._accordionTimeline = tl;

    // Event klik
    item.addEventListener('click', (e) => {
      if (e.target.closest('.faq-item-content')) return;

      const isOpen = !tl.reversed();

      if (!allowMultipleOpen) {
        items.forEach((other) => {
          if (other !== item && !other._accordionTimeline.reversed()) {
            other.classList.remove('is-active');
            other._accordionTimeline.reverse();
          }
        });
      }

      if (isOpen) {
        item.classList.remove('is-active');
        tl.reverse();
      } else {
        item.classList.add('is-active');
        tl.play();
      }
      refreshScroll();
    });

    // === Auto open item pertama ===
    if (index === 0) {
      item.classList.add('is-active');
      tl.play(0);
    }
  });
}

