export function initNavigation() {
  const nav = document.querySelector('.navbar');
  const toggle = document.querySelector('.nav-toggle');
  const navWrap = nav.querySelector('.nav-mobile');
  const menu = navWrap.querySelector('.nav-menu');
  const link = menu.querySelectorAll('.nav-link');
  const btn = navWrap.querySelector('.btn');

  let isOpen = false;

  gsap.set(navWrap, {
    y: '0vh',
  });

  gsap.set(link, {
    autoAlpha: 0,
    x: '-1em',
  });

  gsap.set(btn, {
    autoAlpha: 0,
    y: '0em',
  });

  // Timeline untuk buka tutup menu

  const tl = gsap.timeline({ paused: true });

  // Animasi buka menu
  tl.to(
    navWrap,
    {
      y: '100vh',
      duration: 0.8,
      ease: 'primary-ease',
    },
    0
  );
  tl.to(
    link,
    {
      autoAlpha: 1,
      x: '0em',
      stagger: 0.03,
      duration: 1.2,
      ease: 'primary-ease-out',
    },
    0
  );

  tl.to(
    btn,
    {
      autoAlpha: 1,
      y: '0em',
      duration: 1.2,
      ease: 'primary-ease-out',
    },
    0
  );

  // Hamburger morph
  tl.to(
    '.line.top',
    {
      morphSVG: 'M3.9 15L15.5 5', // garis miring /
      duration: 0.4,
    },
    0
  )
    .to(
      '.line.bottom',
      {
        morphSVG: 'M4.3 4.6L15.2 15.5', // garis miring \
        duration: 0.4,
      },
      0
    )
    .to(
      '.line.middle',
      {
        opacity: 0,
        duration: 0.2,
      },
      0
    );

  toggle.addEventListener('click', () => {
    if (isOpen) {
      // Tutup menu → cukup reverse tanpa scramble
      tl.reverse();
    } else {
      // Buka menu
      tl.play();
    }
    isOpen = !isOpen;
  });
}

export function initAbout() {
  const about = document.querySelector('.hero-about');
  if (!about) return;
  const aboutText = about.querySelector('.text-24.m-14');

  const splitText = new SplitText(aboutText, { type: 'chars, words', charsClass: 'chars' });
  const { words } = splitText;

  const images = about.querySelectorAll('.heading-about-img-wrap');

  gsap.fromTo(
    words,
    { opacity: 0.2 },
    {
      opacity: 1,
      stagger: {
        each: 0.4,
        from: 'start',
      },
      ease: 'none',
      scrollTrigger: {
        trigger: about,
        start: 'top center',
        end: 'bottom center',
        scrub: true,
        markers: false,
      },
    }
  );

  images.forEach((img) => {
    const imgInner = img.querySelector('.about-text-img');

    gsap.fromTo(
      img,
      { opacity: 0.8 },
      {
        opacity: 1,
        ease: 'none',
        scrollTrigger: {
          trigger: img,
          start: 'center 50%',
          end: 'bottom 50%',
          markers: false,
          onEnter: () => imgInner.classList.add('is-active'),
          onEnterBack: () => imgInner.classList.add('is-active'),
          onLeaveBack: () => imgInner.classList.remove('is-active'),
        },
      }
    );
  });
}

export function initLoops() {
  const headLight = document.querySelectorAll('.heading-img.is-main.is-animated');
  if (!headLight.length) return;

  headLight.forEach((img) => {
    gsap.to(img, {
      rotation: 360, // putar penuh
      repeat: -1, // infinite
      ease: 'none', // biar konstan, gak ada akselerasi
      duration: 5 + Math.random() * 3, // durasi random biar gak sinkron semua
      transformOrigin: '50% 50%', // pusat rotasi di tengah
    });
  });
}

